#header {
    background: url(../img/imagen1.png) top/ cover no-repeat;
}

h1, h2, h3, h4, h5 {
    font-family: 'Comic Neue', cursive;
    font-family: 'Kumbh Sans', sans-serif;
    font-family: 'Open Sans', sans-serif;
    font-family: 'PT Sans', sans-serif;
}

#riboon{
    background-color: #94715a;
    background-image: repeating-linear-gradient(45deg, transparent, transparent 25px, rgb(94, 65, 65) 25px 40px);
    max-height: 300px;
}

#riboon::after{
    content: '';
    background-color: transparent;
    display: block;
    height: 10px;
    width: 50%;
    margin: 0 auto;
    position: relative;
    left: auto;
    margin-top: -40px;
}

#cube{
    background-color: #31271e;
    position: relative;
    margin-top: -20px;
    z-index: 10;
    box-shadow: 0 0 10px rgba(48, 10, 10, 0.5);
    max-height: 360px;
}

#tamaño{
    max-height: 351.5px;
    max-width: 300px;
}

.full-screen {
    width: 100vw;   /* Ancho del viewport */
    height: 100vh;  /* Alto del viewport */
    background-color: lightblue; /* Solo para visualización */
  }