@import url(https://fonts.googleapis.com/css2?family=Comic+Neue:ital@0;1&family=Kumbh+Sans:wght@400;700&family=Open+Sans&family=PT+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  max-width: 500px;
  width: 100%;
}

.close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body {
  display: flex;
  font-family: Arial, sans-serif;
  margin: 0;
}

.sidebar {
  height: 100vh;
  width: 250px;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #111;
  padding-top: 20px;
}

.sidebar h2 {
  color: white;
  text-align: center;
  margin-bottom: 30px;
}

.sidebar ul {
  list-style-type: none;
  padding: 0;
}

.sidebar ul li {
  padding: 15px;
  text-align: center;
}

.sidebar ul li a {
  color: white;
  text-decoration: none;
  display: block;
}

.sidebar ul li a:hover {
  background-color: #575757;
}

.main-content {
  margin-left: 260px; /* same as the sidebar width */
  padding: 20px;
}

.pagination {
  display: flex;
  justify-content: center;
  list-style-type: none;
  padding: 0;
}

.pagination li {
  margin: 0 5px;
}

.pagination li a {
  color: #007bff;
  text-decoration: none;
}

.pagination li a:hover {
  text-decoration: underline;
}

.login-container {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.login-form {
  background-color: #f7f7f7;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);
  width: 100%;
  max-width: 500px; /* Aumentar el máximo ancho */
  margin: 450px; /* Centrar horizontalmente */
}

.register-form {
  background-color: #f7f7f7;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);
  width: 100%;
  max-width: 500px; /* Aumentar el máximo ancho */
  margin: 0 auto; /* Centrar horizontalmente */
}

body {
  font-family: 'Open Sans', sans-serif;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#header {
    background: url(/ecommercesoa-react-cliente/static/media/imagen1.c7b1fb00.png) top/ cover no-repeat;
}

h1, h2, h3, h4, h5 {
    font-family: 'Comic Neue', cursive;
    font-family: 'Kumbh Sans', sans-serif;
    font-family: 'Open Sans', sans-serif;
    font-family: 'PT Sans', sans-serif;
}

#riboon{
    background-color: #94715a;
    background-image: repeating-linear-gradient(45deg, transparent, transparent 25px, rgb(94, 65, 65) 25px 40px);
    max-height: 300px;
}

#riboon::after{
    content: '';
    background-color: transparent;
    display: block;
    height: 10px;
    width: 50%;
    margin: 0 auto;
    position: relative;
    left: auto;
    margin-top: -40px;
}

#cube{
    background-color: #31271e;
    position: relative;
    margin-top: -20px;
    z-index: 10;
    box-shadow: 0 0 10px rgba(48, 10, 10, 0.5);
    max-height: 360px;
}

#tamaño{
    max-height: 351.5px;
    max-width: 300px;
}

.full-screen {
    width: 100vw;   /* Ancho del viewport */
    height: 100vh;  /* Alto del viewport */
    background-color: lightblue; /* Solo para visualización */
  }
